import React from 'react';
import Discription from '../discription';
import Footer from '../footer.js';
import Loading from '../loading/loading';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import arrow from '../Signin/arrow.png'


var hash;

class Register extends React.Component {
	constructor(props){
		super(props);
		this.state={
			registerEmail:'',
			registerPassword:'',
			registerName:'',
			err:'',
			registeryear:'I yr.',
			registerbranch:'I.T.',
			registerNumber:'',
			registerGender:'-',
			registerCollege:'GCOEA',
			checkedm:false,
			checkedf:false,
		};
	}

	 error=(err)=>{
	 	this.setState({err:err});
	 }

	 errorCall=()=>{
	 	if(!this.state.registerEmail){this.setState({err:'Enter Email address'})}
	 		else {
	 			if(!this.state.registerNumber)this.setState({err:'Enter mobile number'})
	 				else{
	 					if(!this.state.registerName)this.setState({err:'Enter Name'})
	 						else{
	 							if(!this.state.registeryear)this.setState({err:'Enter year'})
	 								else{
	 									if(!this.state.registerPassword)this.setState({err:'Enter password'})
	 								}
	 						}
	 				}
	 		}
	 }

	 Onbranchregister=(event)=>{
	 	this.setState({registerbranch:event.target.value})
	 }

	  OnCollegeregister=(event)=>{
	 	this.setState({registerCollege:event.target.value})
	 }

	 OnGenderRegister=(s)=>{
	 	if(s==='f'){
	 		this.setState({checkedm:false})
	 		this.setState({checkedf:true})
	 	}
	 	if(s==='m'){
	 		this.setState({checkedm:true})
	 		this.setState({checkedf:false})
	 	}
	 	this.setState({registerGender:s})
	 }

	 Onnumberregister=(event)=>{
	 	this.setState({registerNumber:event.target.value})
	 	
	 }

	 Onyearregister=(event)=>{
	 	this.setState({registeryear:event.target.value})
	 	
	 }



		OnEmailregister=(event)=>{
			this.setState({registerEmail:event.target.value})
		}
		
		OnNameregister=(event)=>{
			this.setState({registerName:event.target.value})
		}


		OnPasswordregister=(event)=>{
			this.setState({registerPassword:event.target.value})
		}

		onSubmitRegiser=()=>{
			this.props.time(true);
			this.errorCall();
			fetch('https://quiet-escarpment-25332.herokuapp.com/register',{
				method:'post',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
					email:this.state.registerEmail.toLowerCase(),
					name:this.state.registerName,
					password:this.state.registerPassword,
					number:this.state.registerNumber,
					branch:this.state.registerbranch,
					year:this.state.registeryear,
					gender:this.state.registerGender,
					college:this.state.registerCollege
				})
			}).then(response=>response.json(),console.log(this.state))
			.then(data=>{
				if(data==='sucess'){
					this.props.insign('signin');
					this.props.time();
					this.props.onResponding('Registered Sucessfully Signin to continue')
				}else{console.log('heplu')}
				}).catch(err=>{this.error('check your internet connection');
									this.props.onResponding('check your internet connection')})
			
	
				
			
		}

	render(){
		const {onsign, time, load}=this.props;
		const {checkedm, checkedf}=this.state;
	return(
		<div className="mt0">
    <p className='red w-33 h2 f3 center'>{this.state.err}</p>                    
	<article className="br3 ba black-10 tc mv4 w-100 w-50-m bg-white w-25-l mw6 center shadow-5">

		<legend className="signin white bg-navy">Register</legend>
<main className="pa4 black-80">
		<div className="measure center">
		<Loading color="bg-near-black" load={load}/>
		<fieldset id="sign_up" className="ba b--transparent ph0 mh0">
		<div className="mt3">
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100"
		type="text"
		name="name"  
		id="name"
		placeholder="Name"
		onChange={this.OnNameregister}
		/>
		</div>
		<div className="mt3">
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="email" 
		name="email-address"  
		id="email-address"
		placeholder="Email"
		onChange={this.OnEmailregister}
		/>
		</div>
		<div className="mt3">
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="number" 
		name="mobile-number"  
		id="mobile-number"
		placeholder="Mobile Number"
		onChange={this.Onnumberregister}
		/>
		</div>
		<div className="mt3 dib" >
		<label className=" fw6 lh-copy black-60 mb2 f6 mr2 pr2" htmlFor="year">College</label>
		<select name='college'
		className='ma0 mb3 mb0'
		onChange={this.OnCollegeregister}>
    <option value="GCOEA">GCOEA</option>
    
    	
		</select><br/>
		<label className=" fw6 lh-copy f6 mr2  black-60 " htmlFor="year">Year</label>
		<select name='Year'
		onChange={this.Onyearregister}>
    <option value="I yr.">I</option>
    <option value="II yr.">II</option>
    <option value="III yr.">III</option>
    <option value="IV yr.">IV</option>
    	
		</select>
		<label className="dib  fw6 lh-copy f6 ml2 mr2  black-60 " htmlFor="password">Branch</label>
		<select name='Branch'
		onChange={this.Onbranchregister}>
    <option value="I.T.">I.T.</option>
    <option value="C.S.">C.S.</option>
    <option value="Instru">Instru</option>
    <option value="Mech">Mech</option>
    <option value="Civil">Civil</option>
    <option value="EXTC">EXTC</option>
    <option value="ELPO">ELPO</option>
  </select>
  </div>
  <div>
  <label className="dib  fw6 lh-copy f6 ml2 mr2 mt2 di  black-60 " htmlFor="password">Gender</label>
  <label className="dib  fw6 lh-copy f6 ml2 mr2  black-70 " >Male</label><input type='radio' value='male' checked={checkedm} onChange={()=>this.OnGenderRegister('m')}/>
	 <label className="dib  fw6 lh-copy f6 ml2 mr2  black-70 " >Female</label><input type='radio' value='female' checked={checkedf} onChange={()=>this.OnGenderRegister('f')}/>
		<br/>
		
		</div>
		<div className="mv3">
		<input 
		className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="password" 
		name="password"  
		id="password"
		placeholder="Password"
		onChange={this.OnPasswordregister}
		/>
		</div>
		</fieldset>
		<div className='bg-navy br-100 h2 w2 center' onClick={this.onSubmitRegiser}>
		<img src={arrow} className='pa1'  width='20'/>
		</div>
		
		</div>
</main>
</article>
</div>	);
}
}
export default Register;