import React from 'react';
import TimeAgo from 'react-timeago';
import f from '../../female.png';
import m from '../../male.png';
import './userprofile.css';
import cross from '../../../cross.png';
import Model from '../../../Model/Model';
import edit from '../../edit.ico'

const userProfile = (props) => {
const	{email, k,picChose, s, uploadClick, click, iu,checkFollow,isFollowing,followClick,userFollowing,modalClosed}=props;
let isFollow=false	
console.log(iu)
return(
<div className='stc' key={k+92}>{iu===undefined?<div className='userprofile shadow-5 ma3 bg-white pa3 ba-black-80 center'></div>:
<div key={k}>
{userFollowing.map((follow,i)=>{
  	 	if(userFollowing[i].following===iu.email){
  	 	
  	 	isFollow=true;
  	 }
  	 })}
<div className='bg-white ba-black-80 center userp h7' key={k+999}>
<div className='w-100 h1 mt2-l'><img src={cross} className='grow z-max fr' height='20px' onClick={()=>{modalClosed()}} width='20px'/></div>
<br/>
<Model show={s} className={'Modal'}  modalClosed={props.Click} key={k*7}><input onChange={picChose} type='file' key='image' id='image' name='image '></input><button className="f6 link dim fr ph3 pv2 mb2 dib white bg-navy" onClick={uploadClick}>Upload</button></Model>
{iu.pic!==null?<img alt='abc' className='fr mr1  br-100  ma3 shadow-5 w-20-l w-20-m w-30'  src={`https://easystudies.herokuapp.com/${iu.pic}`}/>:<img alt='abc' className='fr mr1  pa3 ma3  w-20-l w-20-m w-30'  src={`https://robohash.org/${iu.id}?200*200`}/>}


<div className="black-80 ml4-l ml4-m w-40 "><p className='f3-m f3-l f4  '>{iu.name}</p>
<p className='f3-m f3-l f4  '><span className='b'>Branch</span> {iu.branch}</p>
<p className='f3-m f3-l f4  '><span className='b'>Year</span> {iu.year}</p>
<p className='f3-m f3-l f4  '><span className='b'>Gender</span> {iu.gender==='f'?<img src={f} height='19em' className='pa1 pb0'/>:<img src={m} height='19em' className='pa1 pb0'/>}</p>
<p className='f3-m f3-l f4 pointer' key={k+8}><span className='b' >Followed</span > by {iu.followers}</p>
<p className='f3-m f3-l f4 pointer' key={k+8}><span className='b' >Following</span > {iu.following}</p>
<p className='f3-m f3-l f4  '><span className='b'>Joined </span><TimeAgo date={iu.joined}  /></p>

</div>
<div key={k+32} className='fr pointer'>{isFollow===false?<p key={k+320} className='w3 tc f6 grow ba br3 navy bb-navy pt2 pb2 mb2 dib ' onClick={()=>{followClick(true)}}>Follow</p>:<p key={k+329} className='w3 pr1 pl1 f6 grow ba br3 bg-navy white pt2 pb2 tc mb2 dib ' onClick={()=>{followClick(false)}}>Unfollow</p>}
</div>
{email===iu.email?<p key={k+320} className='w3 tc mr2 f6 grow ba br3 fr bg-navy white pt2 pb1 mb2 dib'>Edit<img src={edit} className='fr pa1  pl0' height='12' width='12' onClick={props.Click}/></p>:null}

</div>
</div>}
</div>
	)
	}
export default userProfile;
