import React from 'react';

const FilterList = ({filterusers,getUser}) =>{
if(filterusers!==undefined) {return(
		filterusers.map((f,index)=>{
			return(<div>
					{index<=5?<p onClick={()=>getUser(f)} className='bb bg-white black pointer tc' key={f.name}>{f.name}</p>:null}
		</div>)
										})

						)}
else{return(<p/>)}


}


export default FilterList;