import React from 'react';
import ReactLoading from 'react-loading';
import './loading.css';
const Loading = ({color, load, x, y}) => (
	<div  className='pt0 loading z-max'>
	{load===true?
		<div >
	<ReactLoading className='center load' type='spinningBubbles' color={color} height={x} width={y}/>
	
	</div>:<p/>}
	</div>
	)
export default Loading;