import React from 'react';
import UsersList from './userslist/userslist'
import './users.css'
import UserProfile from './userprofile/userprofile'
import Profile from './profile'
const Users=(props)=>{
	return(
		<div className='users center w-100 w-80-m w-80-l t4'>
		<div className='w-100  center' >
		<p className='h1 tc b pa4 pt2 ba f3-l f4-m f5-ns f6 w-30-l w-30-m w-40 center br4'>All Users</p>
		<UsersList users={props.users} userSelect={props.userSelect}/>
		<span className='Blue fr'>* only verified users are shown in this list.</span>
		</div>
		<Profile email={props.email} s={props.s} Click={props.Click} k={props.k} picChose={props.picChose} uploadClick={props.uploadClick} users={props.users} iu={props.iu} followClick={props.followClick} userFollowing={props.userFollowing} checkFollow={props.checkFollow} isFollowing={props.isFollowing} show={props.show} modalClosed={props.modalClosed}/>
		
		</div>
			)}
export default Users;