import React from 'react';
import Feed from './feed';
import Loading from '../loading/loading';
import Footer from '../footer';
import MbMenu from '../home/mbmenu/mbmenu';
import option from '../option.png';
import cross from '../cross.png'
import TimeAgo from 'react-timeago';
import m from './male.png';
import f from './female.png';
import edit from './edit.svg';
import SearchBox from './searchBox';
import robots from './robots';
import FilterList from './filterlist';
import Profile from './users/profile'
import QuestionInput from './questioninput/questioninput';
import Users from './users/users';
import logo from '../logo.png';
import Upcomings from './upcomings/upcomings';
import Model from '../Model/Model1';
import Model1 from '../Model/Model';
import FormData from 'form-data';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import {Route,Switch} from 'react-router-dom';
import QuestionBox from './questioninput/QuestionBox/QuestionBox'
let count=0;
const intialstate={
			name:'',
			asked:'',
			response:'',
			Catagory:'Stupid Sction',
			subCatagory:'',
			key:'',
			key1:'',
			isCalled:false,
			show:false,
     		 isCorY:'',
    	  calld:'',
   		   iu:'',
   		   is:false,
   		   users:'',
   		   feedback:'',
   		   question:'',
   		   t:false,
   		   showUsers:false,
   		   specificuser:undefined,
   		   isFollowing:false,
   		   showp:false,
   		   userFollowing:'',
   		   u:'false',
   		   showFile:false,
   		   file:'',
   		   formdata: new FormData(),
   		   showAsk:false,
   		  
}
var userFollowing;
var userFollower;
var filterusers;
var ques;
var formdata= new FormData();
class Home extends React.Component {	
constructor(props){
		super(props);
		this.state={intialstate,
			searchfield:'',
			uname:this.props.user.name,
			 sh:true,
			  picUpload:false,
	}
		this.handleChange = this.handleChange.bind(this);
	}

toShow=()=>{
	this.setState({show:!this.state.show})
}

clickAttach=()=>{
	this.setState({showFile:!this.state.showFile})
}

  catagorySelect=(data)=>{
    this.setState({isCalled:true})
    this.setState({isCorY:'c'})
    this.setState({calld:data})
    
  }

  ts=()=>{
  	this.setState({sh:false})
  }

  toShowUsers=()=>{
  	this.setState({showUsers:!this.state.showUsers})
  }

  toShowUsers1=()=>{
this.setState({showUsers:false})
  }

 specifieduser=(data)=>{
 	this.setState({showp:true})
 	this.time1(true)
 	this.setState({specificuser:data})
 	this.time1()
 }
 modalClosed=()=>{
 	
 	this.setState({showp:false})
 }

getUser=(f)=>{
filterusers=undefined;
 this.setState({iu:f});
 this.setState({is:true})
 this.setState({specificuser:f})
this.setState({showp:true})
  }



searchChange=(event)=>{
this.setState({searchfield:event.target.value})
this.filteru();
if(this.state.searchfield===''){
	filterusers=undefined;
}
}

questionChange=(event)=>{
	this.setState({question:event.target.value})
}

sets=()=>{
	this.setState({t:false})
}

questionSearch=()=>{
	this.setState({t:true})
	this.setState({it:true})
}

yearSelect=(data)=>{
    this.setState({isCalled:true})
    this.setState({isCorY:'y'})
    this.setState({calld:data})
    
  }

  IsCalled=()=>{
    this.setState({isCalled:false})
    this.props.MenuCall();
  }

  checkFollow=(a)=>{
  	var ct=0;
  	if(ct===0){
  	 
  	 } 
  }

  followClick=()=>{
  	this.time1(true);
  	fetch('https://quiet-escarpment-25332.herokuapp.com/flw',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.specificuser.email,
        email1:this.props.user.email
             })
  }).then(response=>response.json())
  	.then(data=>{
  		if(data==='followed'){
  		fetch('https://quiet-escarpment-25332.herokuapp.com/flwer',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.specificuser.email,
        email1:this.props.user.email,
        inc:true
             })
    })
        fetch('https://quiet-escarpment-25332.herokuapp.com/flwng',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.specificuser.email,
        email1:this.props.user.email,
        inc:true
             })
    })
    this.setState({...this.state.iu,
					followers:this.state.iu.followers+1
						    })
    this.allUsers();
	this.followlist();
    this.setState({key1:Math.random()})
        NotificationManager.success('successfully', 'Followed');
        this.time1();
  		}else{if(data==='unfollowed'){
  		  			fetch('https://quiet-escarpment-25332.herokuapp.com/flwer',{
  		        method:'post',
  		        headers:{'Content-Type':'application/json'},
  		        body:JSON.stringify({
  		        email2:this.state.specificuser.email,
  		        email1:this.props.user.email,
  		        inc:false
  		             })
  		        })
  		        fetch('https://quiet-escarpment-25332.herokuapp.com/flwng',{
  		        method:'post',
  		        headers:{'Content-Type':'application/json'},
  		        body:JSON.stringify({
  		        email2:this.state.specificuser.email,
  		        email1:this.props.user.email,
  		        inc:false
  		             })
  		        })
  		  this.setState({
						        ...this.state.iu,
						        followers:this.state.iu.followers-1
						    })
  		  this.allUsers();
	this.followlist();
    this.setState({key1:Math.random()})
  		        NotificationManager.success('successfully', 'Unfollowed');
  		        this.time1();
  		    }
  		}
  	})
  
  .catch(d=>{NotificationManager.error('Error', 'Failed following', 5000, () => {
            alert('callback');
          });})

 	
}
  

resendLink=()=>{
	this.time1(true)
	fetch('https://quiet-escarpment-25332.herokuapp.com/resendLink',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          email:this.props.user.email
             })
  }).then(d=>{
   NotificationManager.success('Link send successfully', 'Sucessful');
this.time1();})
  .catch(d=>{
  	NotificationManager.error('Error', 'Failed sending link', 5000, () => {
            alert('callback');
          });
this.time1();})
}
allUsers=()=>{
	fetch('https://quiet-escarpment-25332.herokuapp.com/users',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(users=>{	
			this.setState({users:users});	
		})
}
followlist=()=>{
		fetch('https://quiet-escarpment-25332.herokuapp.com/flwg',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          email:this.props.user.email
             })
  }).then(response=>response.json())
        .then(d=>{userFollowing=d
        this.setState({userFollowing:d})})

}

ShowAsk=()=>{
	this.setState({showAsk:!this.state.showAsk})
}

componentDidMount(){

	this.allUsers();
	this.followlist();
}

filteru =()=> {
	filterusers=this.state.users.filter(users => {
  			return users.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
  		})

}


FeedbackChange=(event)=>{
this.setState({feedback:event.target.value});
console.log(this.state.feedback)
  }

  FeedbackButton=(e)=>{
    this.time1(true)
    fetch('https://quiet-escarpment-25332.herokuapp.com/feedback',{
        method:'put',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          email:this.props.user.email,
          feedback:this.state.feedback
        })
  }).then(d=>{
   NotificationManager.success('Thank you for submiting Feedback', 'Sucessful');
this.time1();})
  .catch(d=>{
  	NotificationManager.error('Error ', 'Failed submitting feedback', 5000, () => {
            alert('callback');
          });
this.time1();})
  
  }

  fileChose=(event)=>{
  	this.setState({file:event.target.files[0]})

  }
picChose=(event)=>{
	this.setState({file:event.target.files[0]})
}

  handleChange(checked) {
    this.setState({ checked });
    if(checked){
    	this.setState({uname:'Anonymous'})
    }
    else{
    	this.setState({uname:this.props.user.name})
    	 }
  }
 
	onAsking=(event)=>{
		this.setState({asked:event.target.value})
	}

	onCatagoryChange=(event)=>{
		this.setState({Catagory:event.target.value})
		
	}


	subCatagoryChange=(event)=>{
		this.setState({subCatagory:event.target.value})
		
	}

	OnResponse=(response)=>{
		this.setState({response:response});
		setTimeout(this.change,2000);
	}

	time1=(a)=>{
	      this.props.time(a);
	}

	change=()=>{
		this.setState({response:''})
	}

	uClick=()=>{
		this.setState({picUpload:!this.state.picUpload})
		this.setState({key1:Math.random()})
	}

	uploadClick=()=>{
		this.time1(true)
		var formdataU= new FormData();
		if(this.state.file!==''){
		formdataU.append('image',this.state.file);
		this.OnResponse('The profile pic will be uploaded in a few')
		formdataU.append('email',this.props.user.email);
			fetch('https://easystudies.herokuapp.com/file',{
				method:'post',
				body:formdataU
				}).then(d=>{NotificationManager.success('Sucessful', 'Profile pic Uploaded');this.OnResponse('Profile pic Uploaded')
this.time1();
this.setState({picUpload:!this.state.picUpload})
		this.setState({key1:Math.random()})
this.setState({key:Math.random()})})
	.catch(d=>{this.OnResponse('Error Uploading profile pic')
		NotificationManager.error('Error ', 'Failed Uploading profile pic', 5000, () => {
            alert('callback');
          });
this.time1()
this.setState({picUpload:!this.state.picUpload})
		this.setState({key1:Math.random()});})
		}
	}
	 

	onSubmitAsk=()=>{
		this.time1(true)
		var qid;
		formdata.append('image',this.state.file);
		fetch('https://quiet-escarpment-25332.herokuapp.com/pq',{
				method:'put',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
					email:this.props.user.email,
					name:this.state.uname,
					question:this.state.asked,
					catagory:this.state.Catagory,
					subcatagory:this.state.subCatagory,
					joined:new Date(),
				})
	}).then(response=>response.json())
	.then(a=>{if(this.state.file!==''){
		formdata.append('qid',a);
		console.log(this.state.file)
			fetch('https://easystudies.herokuapp.com/file',{
				method:'post',
				body:formdata
				})
		}}).then(d=>{NotificationManager.success( 'Sucessful','Question added to the feed!!');
this.time1();
this.setState({key:Math.random()})})
	.catch(d=>{NotificationManager.error('Error', 'Failed asking question', 5000, () => {
            alert('callback');
          });
this.time1();})
	

	}

	clickUpcoming=()=>{
		this.setState({u:!this.state.u})
	}
	signout=(route)=>{
		if(route==='main'){
		localStorage.setItem('dataa','');}
		this.props.history.push(`/${route}`)
	}

	render(){
		const {user ,onsign, route, load, time, menu,menuCall,userC}=this.props;
		const {isCalled, showSearch,picUpload, showFile, sh, isCorY,calld, show ,key1,searchfield, u, users,is, iu,checked,showUsers, specificuser, isFollowing}=this.state;
		const i =0;
		return(

			<div className='mt0'>
			<NotificationContainer/>
			<header className='App-header pt1 pt2-m pt3-l h4-l h4-m mb0 pb0 mt0 z-999 shadow-5'>
			<h1 className="App-title white relative f6 mbo f5-ns mb0 f4-m f3-l pa1 v-mid mt0 "><span className='pa0'><img src={logo} onClick={this.toShowUsers1} className='h2-l h2-m h1 pointer' height='100'/></span><h2 className='fr pt0 ha mt0 f6 pointer grow' onClick={this.toShowUsers}>{userC[0].count} users..</h2></h1>
			
			{menu===true? <img src={cross} className='white mt0 fl mb0 f7 f6-ns f5-m f5-l pb2 pr2 pt2 pr2-m pr3-l dim pointer' onClick={menuCall} height='25'/>:<img src={option} className='white mt0 fl mb0 f7 f6-ns f5-m f5-l pb2 pr2 pt2 pr2-m pr3-l dim pointer' onClick={menuCall} height='30'/>}	
			
			<p className='center pl2 f5 f5-ns f4-m f4-l mt1 mb0 pointer w5 fl' onClick={()=>{this.getUser(user)}}>Hello {user.name}</p>
					
			<p className='pink v-mid ml5-l ml5-m ml3-ns ml2 tc mb0 mt0'>{this.state.response}</p>
			<div className='dib fr mt0'>
			<a className='link fr f7 f6-ns f5-m f5-l  pr1 pb0 mt1 mb0 pr3-m pr3-l pt2-l pt2-m pt1 dim pointer' onClick={() =>this.signout('main')}> Sign Out</a>
			<a className='link fr f7 f6-ns f5-m f5-l  pr2 pb0 pr3-m mt1 mb0 pr3-l pt2-l pt2-m pt1 dim pointer'onClick={this.clickUpcoming}>Upcomings </a>
			<Upcomings show={u} clicked={this.clickUpcoming}/>
			<div className='fr mb0 modelload mt0 h1 pt0  pb0'><SearchBox  searchfield={this.searchfield} searchChange={this.searchChange}/>{filterusers!==undefined?<div className='fr mr0 br3 mbo pb1 bg-white shadow-5 ba w5 absolute mt4'>	<FilterList getUser={this.getUser} filterusers={filterusers}/></div>:null}</div>
			
			</div>
			</header>
			<MbMenu menuCall={menuCall} menu={menu} handleChange={this.handleChange}
						checked={this.state.checked} catagorySelect={this.catagorySelect} yearSelect={this.yearSelect} questionChange={this.questionChange} questionSearch={this.questionSearch}/>
			
			
					{load===true?
			                   
			                   <Model show={load} className={'Modaload'} ><Loading color="rgb(21, 10, 43)" className='pt0 mt0 mb0' x='25%' y='30%' load={load}/></Model> :<p/>} 
	{is===true?<div><Profile email={user.email} s={picUpload} Click={this.uClick} picChose={this.picChose} uploadClick={this.uploadClick}  k={key1} users={users} iu={iu} followClick={this.followClick} userFollowing={this.state.userFollowing} checkFollow={this.checkFollow} isFollowing={isFollowing} show={this.state.showp} modalClosed={this.modalClosed}/>
						</div>
						:null}
						
			
									
						{this.state.it===true?<h className='f4 f4-ns f3-m f3-l pl2 pa3 b dib w-100 tc relative'>Search reasult for "{this.state.question}" is:</h>:null}
	
			 			{showUsers===true?<Users email={user.email} s={picUpload} Click={this.uClick} k={key1} picChose={this.picChose} uploadClick={this.uploadClick} users={users} modalClosed={this.modalClosed} show={this.state.showp} userFollowing={userFollowing} checkFollow={this.checkFollow} isFollowing={isFollowing} followClick={this.followClick} iu={specificuser} userSelect={this.specifieduser}/>

			 								:
			 								<div>
			 								<div onClick={()=>{this.ShowAsk()}}  className='black-80 grow pointer w-100 w-50-m w-40-l mt0 mr-3 center-l bg-white center-m shadow-5'>
										<h1 className='f4 f4-ns f3-m f2-l pl2 dib w-100 tc relative'>Ask Your Question</h1>
									</div>
{this.state.showAsk===true?
									<Model className={'Modal1 mt0'} modalClosed={this.ShowAsk} show={this.state.showAsk}><QuestionInput
						show={showFile}
						clickAttach={this.clickAttach}
						fileChose={this.fileChose}
						user={user}
						onAsking={this.onAsking}
						onSubmitAsk={this.onSubmitAsk}
						onCatagoryChange={this.onCatagoryChange}
						subCatagoryChange={this.subCatagoryChange}
						handleChange={this.handleChange}
						checked={this.state.checked}
						resendLink={this.resendLink}
						/></Model>:null}
						
									
			 								
						

			 								<Feed user={user}
			 								match={this.props.match}
			 								onsign={onsign}
			 						 			iu={iu}
			 						 			getUser={this.getUser}
			 						 			key={this.state.key}
			 						 			ques={this.state.question}
			 						 			time={this.time1}
			 						 			OnResponse={this.OnResponse}
			 						 			route={route}
			 						 			users={users}
			 						 			uname={this.state.uname}
			 						 			 isCalled={isCalled}
			 						      isCorY={isCorY}
			 						      calld={calld}
			 						      IsCalled={this.IsCalled}
			 						      t={this.state.t}
			 						      sets={this.sets}
			 						      /></div>}
 			
 			<Footer FeedbackChange={this.FeedbackChange} user={user} FeedbackButton={this.FeedbackButton}/>
			</div>)
			
		}
	}
export default Home;